.contact-text-communicate {
  font-family: "latina3";
  font-size: 32px;
  text-align: center;
  color: var(--color-4);
}
.contact-text-queries {
  font-family: "latina1";
  font-size: 15px;
  margin-top: 15px;
  text-align: center;
  color: var(--color-4);
}

.contact-text-whatsapp {
  font-family: "latina1";
  font-size: 15px;
  text-align: center;
  margin-top: 69px;
  margin-bottom: 11px;
  color: var(--color-4);
}
.contact-container-button {
  display: flex;
  justify-content: center;
}
.contact-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 322px;
  height: 48px;
  cursor: pointer;
  outline: none;
  border: 1px solid #1f5d26;
  border-radius: 26px;
  background-color: #1f5d26;
  box-shadow: -2px 5px 8px 0 rgba(0, 0, 0, 0.6);
}
.contact-button:active {
  transform: scale(0.9);
}
.contact-button a {
  display: flex;
  align-items: center;
  font-family: "latina3";
  font-size: 13px;
  letter-spacing: 0.5px;
}
.contact-icon-whatsapp {
  width: 41px;
  height: 34px;
  margin-right: 5px;
}

.contact-p-dfhilo-junior {
  text-align: center;
  margin-top: 11px;
  margin-bottom: 5px;
}
.contact-text-dfhilo {
  font-family: "latina3";
  color: var(--color-4);
  font-size: 21px;
}
.contact-text-junior {
  font-family: "latina1";
  font-size: 21px;
  color: var(--color-4);
  margin-left: 4px;
}
.contact-text-designer {
  font-family: "latina5";
  font-size: 20px;
  text-align: center;
  margin-bottom: 33px;
  color: var(--color-1);
}
.contact-container-background {
  height: 350px;
  background-image: url(../../componentHome/images/artcademi-manito.png),
    url(../../componentHome/images/background-contact.png);
  background-repeat: no-repeat, no-repeat;
  background-position: left 0 top 33px, right 0;
  background-size: 30%, 20%;
  margin-left: -28px;
}
.carrusel-blog {
  height: 460px;
  background-image: url(../../componentHome/images/artcademi-backblog.png);
  background-repeat: no-repeat;
}
@media screen and (min-width: 768px) {
  .container-contact {
    margin-top: 55.5px;
  }
  .contact-button a {
    font-size: 17px;
  }
  .contact-text-dfhilo {
    font-size: 32px;
  }
  .contact-text-junior {
    font-size: 32px;
  }
  .contact-text-designer {
    font-size: 32px;
  }
  .contact-container-background {
    height: 400px;
    background-image: url(../../componentHome/images/artcademi-manitoX2.png),
      url(../../componentHome/images/artcademi-backwhatsappX2.png);
    background-repeat: no-repeat, no-repeat;
    background-position: left 0 top 33px, right 0;
    background-size: 17%, 17%;
    margin-left: initial;
  }
  .carrusel-blog {
    max-width: 100%;
    height: 343px;
    object-fit: cover;
    overflow: hidden;
    background-image: url(../../componentHome/images/artcademi-backblogX2.png);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
  }
}
@media screen and (min-width: 1300px) {
  .contact-container-background {
    background-size: 181px 319px, 179px auto;
  }
  .carrusel-blog {
    height: 588px;
  }
}
